.container_productdetail {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-top: 3rem;
}

.productdetail_left {
  position: relative;
  width: 50%;
  background-color: var(--color-gray-600);
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  padding: 20px;
}

.productdetail_right {
  width: 50%;
}

.productdetail_left_right {
  width: 50%;
  padding: 20px;
  margin-left: 18rem;
  background-color: var(--color-gray-300);
}

.productdetail_left_right img {
  position: relative;
  top: -3rem;
}

.product_left {
  text-align: center;
}

.productdetail_left_left {
  position: absolute;
  width: 49%;
  padding: 20px;
  margin-top: -12rem;
}

.product_image {
  width: 10rem;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.productdetail_left_right h4 {
  font-weight: lighter;
  margin-top: -4rem;
  color: var(--color-gray-100);
  background-color: var(--color-gray-500);
  width: 50%;
  border-radius: 2rem;
  text-align: center;
  margin-left: -1rem;
  font-size: 13px;
}

.productdetail_left_right > p {
  position: relative;
  margin-left: 12rem;
  top: -1.7rem;
  font-size: 1.2rem;
}

.descriere p {
  word-wrap: break-word;
  margin-left: 0.2rem;
  margin-top: 0.5rem;
  font-size: 12px;
  color: var(--color-gray-700);
}

.productdetail_left_left h2 {
  color: var(--color-gray-100);
  position: absolute;
  margin-top: -11rem;
}

.productdetail_left_left h3 {
  position: absolute;
  margin-top: -7.5rem;
  color: var(--color-gray-100);
  background-color: var(--color-gray-500);
  width: 30%;
  border-radius: 2rem;
  text-align: center;
  margin-left: -1px;
  font-weight: lighter;
  font-size: 13px;
}

.productdetail_left_left p {
  margin-top: -5rem;
  font-size: 11.7px;
  color: var(--color-gray-100);
}

.buy_now_btn {
  color: var(--color-gray-100);
  background-color: var(--color-gray-400);
  width: 60%;
  border-radius: 2rem;
  font-size: 30px;
  margin-top: 4rem;
  margin-left: 2rem;
}
.buy_now_btn:hover {
  background-color: var(--color-gray-300);
}

.productdetail_right img {
  width: 20rem;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 8rem;
}

.productdetail_right h2 {
  font-family: "GOLDEN";
  text-align: center;
  margin-top: 7rem;
  font-weight: lighter;
  font-size: 5rem;
  color: var(--color-gray-300);
}
